function menu_sorter(selected_id) {

	//Remove Click for the time being
	$('.main-navigation li a').removeAttr('onclick');

	// Reset to base when clicking home
	if (selected_id == 'home') {
		var toShow = $('.prod-cleansers, .prod-acne, .prod-eczema');
		var textHide = $('.bullet-text-cleanser, .bullet-text-acne, .bullet-text-eczema, .headline-cleanser, .headline-acne, .headline-eczema');
		var textShow = $('.bullet-text, .headline');

		toShow.find('div').css('margin','0 1px');
		toShow.show('slow');
		toShow.promise().done(function() {
			$(textShow).css('display','block');			
			$(textHide).css('display','none');
			$('.main-navigation li a').attr('onclick','menu_sorter(this.id); return false;');
		});
	}

	// Show only cleansers
	if (selected_id == 'cleansers') {
		var toShow = $('.prod-cleansers');
		var toHide = $('.prod-acne, .prod-eczema');
		var textHide = $('.bullet-text, .bullet-text-acne, .bullet-text-eczema, .headline, .headline-acne, .headline-eczema');
		var textShow = $('.bullet-text-cleanser, .headline-cleanser');

		toShow.show('fade');
		toShow.find('div').animate({ 'marginLeft': '10px'},400);
		toShow.promise().done(function(){
			toHide.hide('fade');
			toHide.promise().done(function() {
				toHide.find('div').css('margin','0 1px');
				$(textShow).css('display','block');			
				$(textHide).css('display','none');
				$('.main-navigation li a').attr('onclick','menu_sorter(this.id); return false;');
			});
		});
	}

	// Show only acne
	if (selected_id == 'acne') {
		var toShow = $('.prod-acne');
		var toHide = $('.prod-cleansers, .prod-eczema');
		var textHide = $('.bullet-text-cleanser, .bullet-text, .bullet-text-eczema, .headline, .headline-cleanser, .headline-eczema');
		var textShow = $('.bullet-text-acne, .headline-acne');


		toShow.show('fade');
		toShow.find('div').animate({ 'marginLeft': '10px'},400);
		toShow.promise().done(function(){
			toHide.hide('fade');
			toHide.promise().done(function() {
				toHide.find('div').css('margin','0 1px');
				$(textShow).css('display','block');			
				$(textHide).css('display','none');
				$('.main-navigation li a').attr('onclick','menu_sorter(this.id); return false;');
			});
		});
	}

	// Show only eczema
	if (selected_id == 'eczema') {
		var toShow = $('.prod-eczema');
		var toHide = $('.prod-cleansers, .prod-acne');
		var textHide = $('.bullet-text-cleanser, .bullet-text-acne, .bullet-text, .headline, .headline-acne, .headline-cleanser');
		var textShow = $('.bullet-text-eczema, .headline-eczema');


		toShow.show('fade');
		toShow.find('div').animate({ 'marginLeft': '10px'},400);
		toShow.promise().done(function(){
			toHide.hide('fade');
			toHide.promise().done(function() {
				toHide.find('div').css('margin','0 1px');				
				$(textShow).css('display','block');			
				$(textHide).css('display','none');
				$('.main-navigation li a').attr('onclick','menu_sorter(this.id); return false;');
			});
		});
	}
}